<template>
  <div>
    <transition name="page" mode="out-in">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  created() {
    iframe.goToPage(this.$route.params.pageIndex);
  },

  beforeRouteUpdate (to, from, next) {
    iframe.goToPage(to.params.pageIndex);
    next();
  },
}
</script>

<style scoped>
</style>



